import React, { Component } from 'react';
import './App.css';
import btn_icon_back_giveafeedback from './images/btn_icon_back_giveafeedback.png';

// UI framework component imports
import Button from 'muicss/lib/react/button';
import Appbar from 'muicss/lib/react/appbar';


export default class GiveAFeedbackScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      field: '',
      textarea: '',
    };
  }

  textInputChanged_field = (event) => {
    this.setState({field: event.target.value});
  }
  
  textAreaChanged_textarea = (event) => {
    this.setState({textarea: event.target.value});
  }
  
  onClick_elButton2 = (ev) => {
    // Go to screen 'Give a feedback!'
    this.props.appActions.goToScreen('giveafeedback', { transitionId: 'slideIn_bottom' });
  
  }
  
  
  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_background = {
        width: '100%',
        height: '100%',
     };
    const style_background_outer = {
        backgroundColor: '#f4f4f4',
        pointerEvents: 'none',
     };
    const style_field = {
        display: 'block',
        backgroundColor: 'white',
        paddingLeft: '1rem',
        boxSizing: 'border-box', // ensures padding won't expand element's outer size
     };
    const style_textarea = {
        display: 'block',
        backgroundColor: 'white',
        paddingLeft: '1rem',
        boxSizing: 'border-box', // ensures padding won't expand element's outer size
     };
    const style_fowWho = {
        fontSize: 11.3,
        color: '#444444',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_whatIsYourFeedbac = {
        fontSize: 11.3,
        color: '#444444',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_button2 = {
        display: 'block',
        fontSize: 12.0,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        textAlign: 'center',
        cursor: 'pointer',
     };
    const style_button = {
        display: 'block',
        fontSize: 12.0,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    const style_button3 = {
        display: 'block',
        fontSize: 12.0,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        textAlign: 'center',
        pointerEvents: 'none',
     };
    
    return (
      <div className="AppScreen GiveAFeedbackScreen" style={baseStyle}>
        <div className="background">
          <div className='appBg containerMinHeight elBackground' style={style_background_outer}>
            <div style={style_background} />
          
          </div>
          
        </div>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='baseFont elField'>
            <input style={style_field} type="text" placeholder={this.props.locStrings.feedback_field_788041} onChange={this.textInputChanged_field} defaultValue={this.state.field}  />
          
          </div>
          
          <div className='baseFont elTextarea'>
            <textarea style={style_textarea}  placeholder={this.props.locStrings.feedback_textarea_72546} onChange={this.textAreaChanged_textarea} defaultValue={this.state.textarea}  />
          
          </div>
          
        </div>
        <Appbar className="navBar">
          <div className="title">Give a feedback!</div>  <div className="backBtn" onClick={ (ev)=>{ this.props.appActions.goBack() } }><img src={btn_icon_back_giveafeedback} alt="" style={{width: '50%'}} /></div>
        </Appbar>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className='font-helveticaNeueMedium  elFowWho' style={style_fowWho}>
              <div>{this.props.locStrings.feedback_fowwho_158285}</div>
            </div>
            <div className='font-helveticaNeueMedium  elWhatIsYourFeedbac' style={style_whatIsYourFeedbac}>
              <div>{this.props.locStrings.feedback_whatisyourfeedbac_1040661}</div>
            </div>
            <Button className='elButton2' style={style_button2}  color="accent" onClick={this.onClick_elButton2} >
              {this.props.locStrings.feedback_button2_12880}
            </Button>
            <Button className='elButton' style={style_button}  color="accent" >
              {this.props.locStrings.feedback_button_159387}
            </Button>
            <Button className='elButton3' style={style_button3}  color="accent" >
              {this.props.locStrings.feedback_button3_568862}
            </Button>
          </div>
        </div>
      </div>
    )
  }
  

}



